  line-height: 1.9;
  font-weight: 700;
}
.badge a {
  color: #ffffff;
}

.badge-pill {
  padding-right: 0.875em;
  padding-left: 0.875em;
}

.badge-inline {
  margin-right: 0.625rem;
}
.badge-inline + span {
  top: 2px;
  position: relative;
}
.badge-inline + span > a {
  text-decoration: underline;
}

.badge-md {
  padding: 0.35rem 0.55rem;
}

.badge-lg {
  padding: 0.35rem 0.85rem;
}

.btn .badge-corner {
  position: absolute;
  top: -50%;
  right: 0.5rem;
  transform: translate(50%, 50%);
  margin: 0;
  border: 3px solid;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.wi-tags a {
  display: inline-block;
  padding: 0.125rem 0.875rem;
  margin: 0.25rem;
  line-height: 2;
  font-size: 0.875rem;
  box-shadow: 0 0.125rem 0.25rem rgba(13, 20, 49, 0.07);
  background-color: #ffffff;
}

