  position: relative;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 600;
  right: -15px;
  padding: 4px 12px;
  top: -25px;
  background: #ffffff;
  border-radius: 0.5rem;
  box-shadow: 0 0.125rem 0.25rem rgba(13, 20, 49, 0.07);
}
@media (max-width: 767.98px) {
  .impact-badge {
